/* Montserrat medium (normal) */
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat/500.woff2") format("woff2"), url("../fonts/montserrat/500.woff") format("woff");
}
/* Montserrat bold (normal) */
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/montserrat/700.woff2") format("woff2"), url("../fonts/montserrat/700.woff") format("woff");
}
/* Old Standart TT regular (normal) */
@font-face {
  font-family: "Old Standard TT";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/old-standard-tt/400.woff2") format("woff2"), url("../fonts/old-standard-tt/400.woff") format("woff");
}
/* retina */
/* @media */
/* fonts */
/* text color */
/* bg color */
html {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}
*::before *::after {
  margin: inherit;
  padding: inherit;
}

body {
  box-sizing: inherit;
  color: #695a59;
  font-size: 18px;
  line-height: 32px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 500;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  min-width: 1366px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto;
}

picture {
  display: flex;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-family: inherit;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 12px;
  line-height: 20px;
  color: #A78587;
  padding: 16px 19px 12px;
  background-color: transparent;
  border: none;
  border-top: 1px solid #A78587;
  border-bottom: 1px solid #A78587;
  text-align: center;
}

.title {
  font-family: "Old Standard TT", serif;
  font-weight: 400;
  text-transform: uppercase;
}
.title--primary {
  font-size: 46px;
  line-height: 64px;
  color: #EEE9E6;
  margin-bottom: 16px;
}
.title--secondary {
  font-size: 42px;
  line-height: 54px;
  position: relative;
  z-index: 0;
}
.title--secondary::before {
  color: #EAE1DC;
  font-size: 140px;
  line-height: 110px;
  position: absolute;
  left: -100px;
  top: 50%;
  transform: translateY(-50%);
  z-index: -1;
}
.title--secondary-white {
  font-size: 42px;
  line-height: 54px;
  position: relative;
  z-index: 0;
  color: #EEE9E6;
}
.title--secondary-white::before {
  color: #EAE1DC;
  font-size: 140px;
  line-height: 110px;
  position: absolute;
  left: -100px;
  top: 50%;
  transform: translateY(-50%);
  z-index: -1;
}
.title--secondary-white::before {
  color: rgba(154, 133, 132, 0.1);
}

.map__wrapper {
  padding: 20px;
}

.map__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.map__link {
  color: #000000;
  font-size: 18px;
  line-height: 1.3;
  text-decoration: none;
}

@media (min-width: 480px) {
  .map__wrapper {
    padding: 30px;
  }
  .map__link {
    font-size: 22px;
    line-height: 1.4;
  }
}
@media (min-width: 560px) {
  .map__wrapper {
    padding: 40px;
  }
  .map__link {
    font-size: 22px;
  }
}
@media (min-width: 640px) {
  .map__wrapper {
    padding: 40px;
  }
  .map__link {
    font-size: 24px;
    line-height: 1.5;
  }
}
@media (min-width: 760px) {
  .map__wrapper {
    padding: 40px;
  }
  .map__link {
    font-size: 26px;
    line-height: 1.6;
  }
}
@media (min-width: 1024px) {
  .map__wrapper {
    padding: 50px;
  }
  .map__link {
    font-size: 28px;
  }
}
.container {
  max-width: 1166px;
  padding-left: 100px;
  padding-right: 100px;
  margin: 0 auto;
}

.header {
  background-color: #F8F6F5;
}

.header__wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
  gap: 24px;
  padding: 22px 0;
}

.header__logo {
  grid-column: 1/5;
}

.header__navigation {
  grid-column: 7/12;
}

.header__expand {
  grid-column: 12/-1;
  margin-left: auto;
}

.navigation__list {
  width: 470px;
  display: grid;
  grid-template-columns: 174px 272px;
  align-items: start;
  gap: 24px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.navigation__item {
  display: grid;
  grid-template-columns: 18px auto;
  column-gap: 10px;
}

.navigation__text,
.navigation__link {
  grid-column: 2/-1;
  color: inherit;
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
}

.hero {
  background-color: #EEE9E6;
  background-image: url("../img/hero/background.jpg");
  background-repeat: repeat-x;
  background-size: 100% 520px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .hero {
    background-image: url("../img/hero/background@2x.jpg");
  }
}

.hero__wrapper {
  padding-top: 80px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 24px;
}

.hero__content {
  grid-column: 2/7;
}

.hero__text {
  color: #EEE9E6;
  opacity: 0.6;
  margin-bottom: 36px;
}

.hero__picture {
  grid-column: 7/-1;
}

.about {
  background-color: #EEE9E6;
}

.about__wrapper {
  padding-top: 120px;
  padding-bottom: 132px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 40px;
}

.about__content {
  grid-column: 2/8;
}

.about__text {
  margin-bottom: 16px;
}
.about__text:last-of-type {
  margin-bottom: 0;
}

.about__title {
  grid-column: 2/-1;
  margin-bottom: 49px;
}
.about__title::before {
  content: "about us";
}

.about__quote {
  grid-column: 1/-1;
}

.about__info {
  grid-column: 9/12;
}
.about__info picture {
  margin-bottom: 16px;
}

.about__figure {
  grid-column: 2/12;
  display: grid;
  grid-template-columns: 672px auto;
  align-items: end;
  gap: 24px;
}

.about__caption {
  font-size: 14px;
  line-height: 24px;
  width: 256px;
  position: relative;
  padding-left: 19px;
}
.about__caption::before {
  content: "";
  width: 1px;
  height: 100%;
  background-color: #A78587;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}

.about__link {
  color: #9A8584;
}

.best {
  background-color: #EEE9E6;
}

.best__wrapper {
  padding-bottom: 132px;
}

.best__list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  align-items: start;
  gap: 20px;
}

.best__item {
  display: grid;
  background-color: #ffffff;
}
.best__item h3 {
  font-family: "Old Standard TT", serif;
  font-weight: 400;
  font-size: 32px;
  line-height: 36px;
  margin-bottom: 20px;
}
.best__item span {
  font-size: 22px;
  line-height: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  display: block;
}
.best__item p {
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 32px;
}
.best__item a {
  display: flex;
  align-items: center;
  color: #9A8584;
  font-weight: 700;
  font-size: 12px;
  line-height: 18px;
  text-transform: uppercase;
}
.best__item svg {
  margin-left: 16px;
}
.best__item--tile {
  grid-template-columns: auto;
  grid-column: span 4;
}
.best__item--tile .best__content {
  padding: 36px 36px 40px;
}
.best__item--row {
  grid-template-columns: 178px 1fr;
  grid-column: span 6;
  gap: 20px;
}
.best__item--row .best__content {
  padding: 36px 40px 40px 36px;
}

.best__title {
  margin-bottom: 34px;
}
.best__title::before {
  content: "the best";
}

.projects {
  background-color: #EEE9E6;
}

.projects__wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  padding-bottom: 120px;
}

.projects__title {
  grid-column: 2/-1;
  margin-bottom: 34px;
}
.projects__title::before {
  content: "projects";
}

.slider {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column: 1/-1;
  gap: 20px;
}

.slider__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  grid-column: 2/12;
  display: flex;
  overflow-x: hidden;
}

.slider__item {
  width: 100%;
  flex-shrink: 0;
}

.slider__figure {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 560px auto;
}
.slider__figure picture {
  grid-row: 1/-1;
  grid-column: 1/-1;
}

.slider__information {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 20px;
  padding: 32px 36px;
  align-items: center;
  background-color: rgba(52, 50, 53, 0.9);
  grid-row: 2/-2;
  grid-column: 1/-1;
}

.slider__caption {
  font-family: "Old Standard TT", serif;
  font-size: 32px;
  line-height: 36px;
  color: #F5F2ED;
}

.slider__link {
  display: flex;
  align-items: center;
  font-size: 12px;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
  color: #F5F2ED;
}
.slider__link svg {
  margin-left: 16px;
}

.slider__button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #343235;
  width: 72px;
  height: 72px;
  border-radius: 50%;
  align-self: center;
  grid-column: 1/2;
  grid-row: 1/2;
  cursor: pointer;
}
.slider__button--previous {
  justify-self: start;
}
.slider__button--next {
  justify-self: end;
  grid-column: 12/-1;
  transform: rotate(-180deg);
}

.mission {
  background-color: #343235;
  background-image: url("../img/mission/background.jpg");
  background-size: 100% 320px;
  background-repeat: repeat-x;
  background-position: 0 80%;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .mission {
    background-image: url("../img/mission/background@2x.jpg");
  }
}

.mission__wrapper {
  padding-top: 120px;
  padding-bottom: 132px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.mission__container {
  display: grid;
  gap: 20px;
}

.mission__picture--pillow {
  justify-self: end;
}
.mission__picture--dog {
  align-self: end;
}

.mission__content {
  padding-left: 100px;
}

.mission__title::before {
  content: "mission";
}

.mission__text {
  color: #FBF3F0;
  margin-bottom: 16px;
}
.mission__text:last-child {
  margin-bottom: 0;
}

.our-team {
  background-color: #343235;
}

.our-team__wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-auto-flow: dense;
  padding-bottom: 120px;
}

.our-team__title {
  grid-column: 2/-1;
  margin-bottom: 36px;
}
.our-team__title::before {
  content: "our team";
}

.our-team__content {
  grid-column: 2/span 6;
  margin-bottom: 40px;
}
.our-team__content p {
  color: #FBF3F0;
  margin-bottom: 16px;
}
.our-team__content p:last-child {
  margin-bottom: 0;
}

.our-team__note {
  font-size: 14px;
  line-height: 24px;
  color: #FBF3F0;
  grid-column: -1/-5;
}

.our-team__picture {
  grid-column: 1/-1;
}

.collections {
  background-color: #EEE9E6;
}

.collections__wrapper {
  padding-top: 158px;
  padding-bottom: 120px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.collections__title {
  grid-column: 1/-1;
  margin-bottom: 54px;
}
.collections__title::before {
  content: "collections";
}

.collections__content {
  grid-column: 2/span 6;
}
.collections__content p {
  margin-bottom: 40px;
}

.collections__list {
  grid-column: 1/-1;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.collections__item--big {
  grid-row-end: span 2;
}

.footer {
  background-color: #343235;
}

.footer__wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
}

.footer__title {
  margin-bottom: 36px;
}
.footer__title::before {
  content: "talk";
}

.footer__content {
  grid-column: 2/span 5;
}
.footer__content p {
  color: #EEE9E6;
  opacity: 0.6;
  margin-bottom: 44px;
}

.footer__fields {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 36px 20px;
  border: none;
  margin-bottom: 60px;
}
.footer__fields li {
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: auto;
  grid-template-row: auto;
  align-items: center;
}
.footer__fields label,
.footer__fields input {
  color: #F6EDE9;
  font-size: 14px;
  line-height: 18px;
  grid-row: 1/-1;
  grid-column: 1/-1;
}
.footer__fields input {
  outline: none;
  border: none;
  background-color: transparent;
  border-bottom: 1px solid #A78587;
  padding-bottom: 16px;
}
.footer__fields input::placeholder {
  color: #F6EDE9;
}

li.footer__field--select {
  grid-column: span 1;
}
li.footer__field--checkbox {
  grid-column: span 1;
  padding-left: 44px;
  position: relative;
}
li.footer__field--checkbox label {
  position: relative;
  cursor: pointer;
}
li.footer__field--checkbox label::before {
  content: "";
  width: 26px;
  height: 26px;
  border: 1px solid #A78587;
  position: absolute;
  top: 50%;
  left: -43px;
  transform: translateY(-50%);
  cursor: pointer;
}
li.footer__field--checkbox input {
  left: -27px;
  bottom: 10px;
}
li.footer__field--checkbox input:checked ~ label::after {
  background-image: url("../img/icons/checked.svg");
  background-size: 32px, 28px;
  content: "";
  width: 32px;
  height: 28px;
  position: absolute;
  top: 50%;
  left: -43px;
  transform: translateY(-50%);
  cursor: pointer;
}
li.footer__field--checkbox input:checked ~ label::before {
  content: none;
}

.footer__tender {
  cursor: pointer;
  width: 100%;
}

.footer__picture {
  grid-column: 8/-1;
}

.select {
  width: max-content;
  border: 1px solid #A78587;
  position: relative;
}

.select__container {
  display: grid;
  grid-template-columns: auto 14px;
  align-items: center;
  gap: 13px;
  padding: 16px 20px;
  cursor: pointer;
}
.select__container svg {
  grid-column: 2/3;
}
.select__container span {
  grid-column: 1/2;
  font-size: 14px;
  line-height: 18px;
  color: #FFFFFF;
}

.select__list {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.select__list a {
  display: block;
  padding: 8px 15px;
  font-size: 14px;
  line-height: 18px;
  color: #FFFFFF;
}

.select__variants {
  margin: 0;
  padding: 0;
  list-style-type: none;
  border-top: none;
  border: 1px solid #A78587;
  width: 100%;
  background-color: #343235;
  position: absolute;
  top: 50px;
  left: -1px;
  display: none;
}
.select__variants li {
  border-bottom: 1px solid #A78587;
}
.select__variants li:last-child {
  border-bottom: none;
}
.select__variants li:first-child {
  border-top: none;
}
.select__variants a {
  display: block;
  padding: 16px 20px;
  font-size: 14px;
  line-height: 18px;
  color: #FFFFFF;
}